@import "../../main.style.scss";

.contact {
  &__wish {
    h2 {
      @include respond(big-desktop) {
        width: 1800px;
        margin-left: auto;
        margin-right: auto;
        padding-left: 10rem;
      }
    }
  }
  &__wishContent {
    display: flex;
    flex-direction: column;
    position: relative;
    overflow: hidden;
    box-shadow: 0 .2rem 2rem .2rem rgba($color-black, .1);

    &--background {
      object-fit: cover;
      height: 50vh;
      opacity: .4;

      @include respond(tab-land) {
        height: 80vh;
      }
    }
  }

  &__wishCardsContainer {
    height: 50vh;
    width: 100%;
    overflow-y: scroll;

    position: absolute;
    top: 0;
    left: 0;

    @include respond(tab-land) {
      height: 80vh;
    }
  }

  &__wishCards {
    margin: 2rem;
    width: calc(100% - 4rem);
    display: grid;
    gap: 2rem;

    @include respond(tab-land) {
      padding: 0 10rem;
    }

    @include respond(desktop) {
      padding:  2rem 24rem;
    }
  }

  &__wishForms {
    @include respond(tab-land) {
      padding:  2rem 6rem;
    }

    @include respond(desktop) {
      padding:  2rem 12rem;
    }
  }


  &__socialMedia {
    margin: 2rem;
    font-size: 2rem;
    
    @include respond(tab-port) {
      padding: 0 10rem;
    }

    @include respond(desktop) {
      h2 {
        margin-left: 0;
      }
    }
  }

  &__links {
    margin-top: 2rem;
    display: grid;
    gap: .8rem;
  }

}

